<template>
    <div class="table">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-lx-cascades"></i> banner列表</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button type="primary" icon="delete" class="handle-del mr10" @click="newAdd">新增banner</el-button>
                <!-- <el-select v-model="select_cate" placeholder="筛选省份" class="handle-select mr10">
                    <el-option key="1" label="广东省" value="广东省"></el-option>
                    <el-option key="2" label="湖南省" value="湖南省"></el-option>
                </el-select> -->
                <!-- <el-input v-model="select_word" placeholder="筛选关键词" class="handle-input mr10"></el-input>
                <el-button type="primary" icon="search" @click="search">搜索</el-button> -->
            </div>
            <el-table :data="allMessageData" border class="table" ref="multipleTable">
                <el-table-column prop="nickname" label="昵称">
                </el-table-column>
                <el-table-column prop="email" label="邮箱">
                </el-table-column>
                <el-table-column prop="webName" label="网站名">
                </el-table-column>
                <el-table-column prop="wenLink" label="网站链接">
                </el-table-column>
                <el-table-column prop="linkType" label="外链类型">
                </el-table-column>
                <el-table-column prop="remark" label="备注">
                </el-table-column>
                <el-table-column prop="ctime" label="创建时间">
                </el-table-column>
                <el-table-column label="状态"  align="center">
                    <template slot-scope="scope">
                        <span style="color:green" v-if="scope.row.codeType==0">未审核</span>
                        <span style="color:#409eff" v-else-if="scope.row.codeType==1">通过</span>
                        <span style="color:red" v-else-if="scope.row.codeType==2">未通过</span>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="150" align="center">
                    <template slot-scope="scope">
                        <el-button type="text" icon="el-icon-edit" @click="editorTag(scope.$index, scope.row)">审核</el-button>
                        <el-button type="text" icon="el-icon-delete" class="red" @click="detailTags(scope.$index, scope.row)">删除</el-button> 
                    </template>
                </el-table-column>
            </el-table>
            


        </div>

        <!-- 编辑弹出框 -->
        <el-dialog :title="visibleTitle" :visible.sync="editVisible" width="700px" @close="closeDia">
            <ul class="linkDia">
                <li><span>昵称：</span>{{linkDataDan.nickname}}</li>
                <li><span>邮箱：</span>{{linkDataDan.email}}</li>
                <li><span>网站名：</span>{{linkDataDan.webName}}</li>
                <li><span>网站链接：</span>{{linkDataDan.wenLink}}</li>
                <li><span>外链类型：</span>{{linkDataDan.linkType}}</li>
                <li><span>备注信息：</span>{{linkDataDan.remark}}</li>
            </ul>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEditOk">通过</el-button>
                <el-button type="primary" @click="saveEditNo">不通过</el-button>
            </span>
        </el-dialog>

        <!-- 删除提示框 -->
        <el-dialog title="提示" :visible.sync="delVisible" width="400px">
            <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delVisible = false">取 消</el-button>
                <el-button type="primary" @click="deleteRow">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import axios from '../../axios'
    export default {
        name: 'basetable',
        data() {
            return {
                is_search: false,
                editVisible: false,
                delVisible: false,
                form: {
                    title:'',
                    abstract:'',
                    // picUrl:'',
                    ctime: '',
                    region:'',
                    isStart:true,
                },
                picBannerUrl:'',
                picBannerUrlName:'',
                tenBlog:[],
                multipleSelection:[],
                isViewPic:false,
                bigImgUrl:'',
                isDetail:'',
                nowBannerId:'',

                visibleTitle:'',
                allTagsData:[],
                tagId:'',
                totalNum:0,
                pageSize:5,
                pageIndex:1,
                content: '',
                allMessageData:[],
                fileList: [],
                linkDataDan:'',

            }
        },
        components:{
            
        },
        created() {
            this.getAllTags();
            this.getBlogTenList();
        },
        computed: {
            data() {
                return this.tableData.filter((d) => {
                    let is_del = false;
                    for (let i = 0; i < this.del_list.length; i++) {
                        if (d.name === this.del_list[i].name) {
                            is_del = true;
                            break;
                        }
                    }
                    if (!is_del) {
                        if (d.address.indexOf(this.select_cate) > -1 &&
                            (d.name.indexOf(this.select_word) > -1 ||
                                d.address.indexOf(this.select_word) > -1)
                        ) {
                            return d;
                        }
                    }
                })
            }
        },
        methods: {
            // 看大图片
            bigImg(val){
                this.isViewPic=true;
                this.bigImgUrl=val
            },
            chaunSuccess(response, file, fileList){
                console.log(response.data.url, file, fileList)
                this.picBannerUrl = response.data.url;
                this.picBannerUrlName = response.data.name;
            },
           handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            //获取前十个博客列表
            getBlogTenList(){
                axios.get('/api/web/queryPageBlog',{
                    params:{
                        pageSize:10,
                        pageIndex:1,
                    }
                })
                        .then(res=>{
                            this.tenBlog=res.data.bloglist;
                            // this.totalNum=res.data.total;
                            console.log(this.blogListData,res);
                        })
            },
            // 获取banner列表
            getAllTags(){
                axios.get('/api/web/queryAllLink')
                    .then(res=>{
                        console.log(res)
                        this.allMessageData=res.data.rows;
                    }).catch(err=>{
                        console.log(err)
                    })
            },
            
        //    删除banner
            detailTags(index, row) {
                this.delVisible=true;
                this.isDetail=row._id;
            },
            detailBanner(row){
                axios.post('/api/web/dellLink',{
                    id:row
                }).then(res=>{
                    if(res.status==200){
                        this.delVisible = false;
                        this.$message({
                            showClose: true,
                            message: '恭喜你，删除成功！',
                            type: 'success'
                        });
                        this.isDetail='';
                        this.getAllTags();
                    }
                    console.log(res);
                })
            },
            // 确定删除
            deleteRow(){
                this.detailBanner(this.isDetail)
            },
            //新增标签
            newAdd(){
                
                this.nullBlog();
                this.visibleTitle='新增banner';
                this.editVisible = true;
                // this.insertBlog();
            },
            closeDia(){
                console.log('关闭了哈哈哈')
                this.fileList=[]
            },
            // 清除form内容
            nullBlog(){
                this.form={
                    title:'',
                    abstract:'',
                    // picUrl:'',
                    ctime: '',
                    region:'',
                    isStart:true,
                }
            },

            
            submit(){
                console.log(this.content);
                this.$message.success('提交成功！');
            },

            // 保存编辑
            saveEditOk() {
                axios.post('/api/web/updateOneLink',{id:this.linkDataDan._id,codeType:1}).then(res=>{
                    if(res.status==200){
                        this.$message({
                            showClose: true,
                            message: '恭喜你，添加成功!',
                            type: 'success'
                        });
                        this.editVisible = false;
                        this.getAllTags();
                    }
                    console.log('添加返回信息：',res);
                })
                
            },
            saveEditNo() {
                axios.post('/api/web/updateOneLink',{id:this.linkDataDan._id,codeType:2}).then(res=>{
                    if(res.status==200){
                        this.$message({
                            showClose: true,
                            message: '恭喜你，添加成功!',
                            type: 'success'
                        });
                        this.editVisible = false;
                        this.getAllTags();
                    }
                    console.log('添加返回信息：',res);
                })
                
            },
            addTags(submitArr){
                axios.post('/api/web/addBanner',submitArr).then(res=>{
                    if(res.status==200){
                        this.$message({
                            showClose: true,
                            message: '恭喜你，添加成功!',
                            type: 'success'
                        });
                        this.editVisible = false;
                        this.picBannerUrl='';
                        this.picBannerUrlName='';
                        this.nullBlog();
                        this.getAllTags();
                    }
                    console.log('添加返回信息：',res);
                })
                        
            },

            // 编辑标签
            editorTag(index,row){
                this.visibleTitle='审核友情链接'
                this.linkDataDan=row;
                this.editVisible=true;
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
                console.log(this.multipleSelection);
            },

        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px; 
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
    .mr10{
        margin-right: 10px;
    }
    .bigTu{
        max-width: 1000px;
    }
    .linkDia{ overflow: hidden;}
    .linkDia li{
        width: 50%;
        float: left;
        font-size: 14px;
        line-height: 30px;
    }
    .linkDia li span{ color: #999}
</style>
